const menuBtn = document.querySelector('#menu-btn');
const closeBtn = document.querySelector('#close-btn');
const menu = document.querySelector('nav .container ul');

//Open menu
menuBtn.addEventListener('click', () => {
	menu.style.display = 'block';
	menuBtn.style.display = 'none';
	closeBtn.style.display = 'inline-block';
})

//Hide menu
closeBtn.addEventListener('click', () => {
	menu.style.display = 'none';
	menuBtn.style.display = 'inline-block';
	closeBtn.style.display = 'none';
})

const navItems = menu.querySelectorAll('li');

const changeActiveItem = () => {
	navItems.forEach(item => {
		const link = item.querySelector('a');
		link.classList.remove('active');
	})
}

navItems.forEach(item => {
	const link = item.querySelector('a');
	link.addEventListener('click', () => {
		changeActiveItem();
		link.classList.add('active');
	})
})

//read more 
const readMoreBtn = document.querySelector('.about__more');
const readMoreContent = document.querySelector('.about__read-more');

readMoreBtn.addEventListener('click', () => {
	readMoreContent.classList.toggle('show-content');
	if(readMoreContent.classList.contains('show-content')){
		readMoreBtn.textContent = 'Show less';
	}else{
		readMoreBtn.textContent = 'Show more';
	}
})

//show hide skills
const skillItems = document.querySelectorAll('.skills .skills__list');

skillItems.forEach(skills__list => {
	skills__list.querySelector('.skills__head').addEventListener('click', () => {
		skills__list.querySelector('.skills__items').classList.toggle('show-items');
	})
})

// BUTTON UP 
window.onscroll = function () { scrolling() };

function topUp() {
	document.body.scrollTop = 0;
	document.documentElement.scrollTop = 0;
}

function scrolling() {
	if (document.body.scrollTop > 20 || document.documentElement.scrollTop > 20) {
		document.getElementById("Btn").style.display = "block";
	} else {
		document.getElementById("Btn").style.display = "none";
	}
}